.footer{
    background: rgb(32, 32, 32);
    z-index: 999;
    display: flex;
    justify-content: center;
    position: relative;
    bottom: 0;
    width: 100%;
    height: 16vh;
    color: white;
    font-size: 1.4rem;
    margin-top: -20px;
}

.footer_container{
    display: flex;
    justify-content: center;
    align-items: center;
}

.linkedinIcon{
    border-radius: 50%;
    background: white;
    width: 9vh;
    height: 9vh;
}

.linkedinIcon:hover{
    background:rgb(65, 65, 65);
}

.footer_container_element{
    margin: 5vh;
}

.website_rights{
    font-weight: bold;
}